<template>
  <div class="hello">

      <div class="card-deck mb-3">

        <div class="card mb-4 color_10" v-for="item in items">

          <div class="card-header text-center" :style="{ 'background-color': item.color }">
            <h4 class="my-0 font-weight-normal">{{ item.bookName }}</h4>
            <h4 style="position: absolute; bottom: 0px; right: 20px">{{ item.year }}</h4>
          </div>

        </div>
      </div>

      <el-dialog
        title=""
        :visible.sync="dialogVisible"
        :modal-append-to-body="false"
        width="95%"
        height="90%"
        :before-close="handleClose">

        <div class="htmlContent" style="text-align: left; margin-left: 50px; margin-top: 0px; overflow-y:auto; height: 600px" v-html="htmlContent"></div>

      </el-dialog>
  </div>
</template>



<script>
import Clock from './Clock'
import axios from 'axios';

export default {
  name: 'Script',
  components: {
        "clock": Clock
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      htmlContent: "abcd",
      dialogVisible: false,
      items: [
          { year: "Claire Weekes • 2012", color: "#a899e6", bookName: "精神焦虑症的自救",  },
          { year: "2013", color: "#dda0dd", bookName: "不吃饱的生活",  },
          { year: "2016", color: "#65f5c3", bookName: "算法导论",  },
          { year: "2017", color: "#6598f5", bookName: "mysql必知必会",  },
          { year: "2018", color: "#f56598", bookName: "程序员面试金典",  },
          { year: "2020", color: "#f5c365", bookName: "head first design pattern",  },
          { year: "2022", color: "#98f565", bookName: "跟着美剧《老爸老妈的浪漫史》学英语",  },
          { year: "2023", color: "#f57b65", bookName: "微积分读本（修订版）",  },
          { year: "2023", color: "#47d78a", bookName: "spring security in action",  },

      ]
    }
  },
  mounted() {
  },
  methods: {
    async handle1() {
        //this.$router.push(`/noteMarkDown/1`)
        this.dialogVisible = true;
        await axios.get("/static/note/1.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handle2() {
        //this.$router.push(`/noteMarkDown/2`)
        this.dialogVisible = true;
        await axios.get("/static/note/2.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    },
    async handle3() {
        this.dialogVisible = true;
        await axios.get("/static/note/3.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    }
  }
}
</script>

<style scoped>
  .hello {
    height: 95%;
    width: 100%;
    position: fixed;
    overflow: auto;
    background-image: linear-gradient(#c7edcc, #54FF9F, #1E90FF, #A020F0);

  }

  .el-container {
    height: 30%;
    background-color: #c7edcc;
    background-image: linear-gradient(#c7edcc, #54FF9F, #1E90FF, #A020F0);
  }


  .font-weight-normal {
      font-weight: 700!important;
  }
  .mb-0, .my-0 {
      margin-bottom: 0!important;
  }
  .mt-0, .my-0 {
      margin-top: 0!important;
  }

  .mb-3, .my-3 {
      margin-bottom: 1rem!important;
  }

  .h4, h4 {
      font-size: 1.1rem;
  }

  .card-deck {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-flow: row wrap;
      flex-flow: row wrap;
      margin-right: -15px;
      margin-left: -20px;
      margin: auto;
      margin-top: 20px;
      float: left;
  }
  .card {
      position: relative;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: column;
      flex-direction: column;
      min-width: 0;
      word-wrap: break-word;
      background-color: white;
      background-clip: border-box;
      border: 0px solid rgba(255,0,0,.125);
      border-radius: 0.25rem;
      margin-left: 100px;
      width: 245px;
      border-radius: 25px;
      height: 150px;
  }

  .el-button {
    float: center;
  }
  .text-center {
      text-align: center!important;
  }

  .text-left {
      text-align: left!important;
  }
  .card-header:first-child {
      min-height: 100px;
      border-radius: 25px;
  }
  .card-header {
      padding: 0.75rem 1.25rem;
      margin-bottom: 0;
      background-color: rgba(0,0,0,.03);
      border-bottom: 1px solid rgba(0,0,0,.125);

      -ms-flex: 1 1 auto;
      flex: 1 1 auto;
      min-height: 1px;
      padding: 1.0rem;
      line-height: 28px;
  }

  .mb-4, .my-4 {
      margin-bottom: 3.0rem!important;
  }

  .mt-3, .my-3 {
      margin-top: 1.5rem!important;
  }
</style>



